.paginationPanel {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 30px;
    height: 5vh;
    background-color: #5b74ec
}

.paginationButton {
    box-sizing: border-box;
    cursor: pointer;
    margin: 0 1vw;
    height: 30px;
    width: 40px;
    background-color: white;
    color: #3255fc;
    border-radius: 5px;
    padding: 5px 10px;
}

.activePage {
    color: white;
    background-color: #303138;
}
